@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";

@import "variables";


// 專屬推薦
.cathay-carosel{
  &.#{$prefix}-layout-03{  
    // margin-top: 20px;
    .#{$prefix}slider{
      >div{

        >.cs-link{
          display: flex;
          flex-wrap: wrap;
          @media (min-width: map-get($grid-breakpoints, 'md')){
            display: block;
          }
        }
        

        + div{
          margin-top: 30px;
          @media (min-width: map-get($grid-breakpoints, 'md')){
            margin-top: 0;
          }
        }
      }
    }    

    .slick-list{
      @media (min-width: map-get($grid-breakpoints, 'md')){
        padding:0 2%;
      }
    }
    .slick-track{
      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin:25px 0;
      }

    }
    .slick-slide{
      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin:0 6px;
        box-shadow: 1px 5px 14px -6px rgba(0,0,0,0.5);
        border-radius: 10px;
        overflow: hidden;
        transition:box-shadow .1s ease-in;
        &:hover{
          box-shadow: 1px 5px 14px -6px rgba(0,0,0,0.93);

          .#{$prefix}img-wrap{
            transform: scale(1.1);
          }
          .btn{
            color: #df780f;
            &:after {
              transform: translate(3px, 0px);
            }
          }
        }
      }
      @media (min-width: map-get($grid-breakpoints, 'lg')){
        margin:0 25px;
      }
    }
    .#{$prefix}title{
      font-size:18px;
      font-weight: 400;
      line-height: 1;
      color:#333333;
      letter-spacing: 2px;
      text-align: left;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        font-size:20px;
        text-align: center;
      }
    }
    .#{$prefix}content{
      font-size:16px;
      line-height: 1.5;
      color:#333333;
      letter-spacing: 1px;
      font-weight: 400;
    }

    .#{$prefix}link{
      display: block;
    }

    .#{$prefix}title + .#{$prefix}content{
      margin-top: 10px;
    }

    .#{$prefix}txt-wrap{
      flex: 1 1;
      margin-left:3.5%;
      margin-right:3%;
      display: block;
      padding:0px;
      position: relative;
      width: 100%;

      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin-left:0;
        margin-right:0;
        padding:15px 15px 35px 15px;
        background-color: #fff;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        padding:15px 15px 35px 15px;
      }
    }

    .#{$prefix}img-wrap{
      flex: 1 1;
      margin-left:3%;
      margin-right:3.5%;
      $ratio:1;
      width: round($ratio * 150px);
      height: round($ratio * 150px);
      background-size:cover;
      overflow: hidden;
      background-repeat: no-repeat;
      // background-position: center top;
      background-position: center center;
      vertical-align: middle;
      border-radius: 10px;
      transition:transform .2s ease-out;
      img{
        visibility: hidden;
      }

      
      @media (min-width: map-get($grid-breakpoints, 'md')){
        margin-left:0;
        margin-right:0;
        border-radius: 0;
        width: 100%;
        height: 140px;
        // background-position: center bottom;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        height: 140px;
        // background-position: center center;
      }

    }

    .#{$prefix}more{
      display: block;
      position: absolute;
      bottom:0;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        position: relative;
        margin-top: 10px;
      }
    }

    .btn{
      height: auto;
      line-height:1;
      padding:0;
      font-weight: 300;
    }
 
  }
}